.first__screen {
  display: block;
  width: 100%;
  height: 100vh;
  padding: 0;
  background: radial-gradient(#779dbf, #14375a);
  overflow: hidden;
}

.first__screen h1 {
  text-align: center;
  color: #fff;
  margin-top: 5rem;
  margin-bottom: -30px;
  font-size: 3rem;
  font-family: 'Exo 2', sans-serif;
  text-shadow: 2px 2px 4px rgba(40, 40, 40, 0.6);
  position: relative;
  left: 130%;
}

.i {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
}

.i__img {
  display: block;
  border-radius: 50%;
  width: 20%;
  height: 20%;
  z-index: 2;
  box-shadow: 10px 10px 70px rgba(180, 150, 150, 0.7);
  overflow: hidden;
  position: absolute;
  top: 40%;
  left: 40%;
}

.i__circle1 {
  display: block;
  width: 100%;
  height: 100%;
}

.i__circle1 img {
  width: 100%;
  height: 100%;
  display: block;
  transform-origin: 50%;
  transform-origin: 50%;
  animation: ani linear infinite 30s;
  z-index: 2;
}

@keyframes ani {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(-180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.i__circle2 {
  display: block;
  width: 70%;
  height: 70%;
  position: absolute;
  left: 15%;
  top: 15%;
}

.i__circle2 img {
  width: 100%;
  height: 100%;
  display: block;
  transform-origin: 50%;
  animation: ani1 linear infinite 24s;
  z-index: 2;
}

@keyframes ani1 {
  0% {
    transform: rotate3d(0, 0, 0, 0);
  }

  59% {
    transform: rotate3d(2500, 1700, 1308, 360deg);
  }

  100% {
    transform: rotate3d(0, 0, 0, 0);
  }
}

.i__circle3 {
  display: block;
  width: 46%;
  height: 46%;
  position: absolute;
  top: 27%;
  left: 27%;
}

.i__circle3 img {
  width: 100%;
  height: 100%;
  display: block;
  transform-origin: 50%;
  animation: ani linear infinite 27s;
  z-index: 2;
}

@keyframes ani {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(-180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

