  justify-content: space-around;
  align-items: center;
  flex-basis: 100%;
}

.oneStr #res {
  font-size: 1rem;
  margin-top: 2rem;
  transform: rotate(15deg);
}

.oneStr .circles {
  fill: none;
  stroke-width: 15;
}

.oneStr .circle_first {
  stroke: #dfdcd5;
}

.oneStr .circle__second {
  stroke: #1bb696;
  stroke-dasharray: .001 282.6;
}

.oneStr .item {
  display: inline-block;
  width: 120px;
  position: relative;
  z-index: 3;
}

.oneStr .num {
  position: absolute;
  top: 50%;
  left: calc(50% - 15px);
  font-family: sans-serif;
  color: #fff;
  z-index: 1;
}

.oneStr #res,
.oneStr h3 {
  color: #fff;
  font-family: sans-serif;
  text-align: center;
}

.contact__screen {
  background: radial-gradient(#3a9464, #1e2a41);
}

.contact__screen .wrapper {
  max-width: 385px;
}

.contact__screen .wrapper .contact {
  margin: 0 auto;
}

.contact__screen .wrapper .contact span {
  display: block;
  color: #fff;
  text-shadow: 1px 1px 1px rgba(71, 71, 71, 0.6);
}

.contact__screen .wrapper .contact span i {
  font-size: 1.5rem;
}

.contact__screen .wrapper .contact span a {
  padding-left: 1rem;
  text-decoration: none;
  color: #fff;
  font-size: 1.1rem;
  text-indent: 2rem;
  font-family: 'Exo 2', sans-serif;
  word-spacing: 1px;
}

.contact__screen .wrapper .contact iframe {
  margin-top: 2rem;
}

@media screen and (max-width: 992px) {
  .first__screen h1 {
    font-size: 2rem;
  }

  .works__screen .wrapper .works .work {
    width: calc(50% - 30px);
  }

  .about__screen .wrapper {
    height: 2000px;
  }
}

@media screen and (max-width: 576px) {
  .first__screen h1 {
    font-size: 1.4rem;
  }

  .first__screen h1 {
    margin-top: -10rem;
  }

  .works__screen .wrapper .works .work {
    width: calc(50% - 30px);
  }

  .about__screen .wrapper {
    height: 3000px;
  }

  .about__screen .wrapper div.about img {
    margin: 0 auto;
  }
}

@media screen and (max-width: 480px) {
  .works__screen .wrapper .works .work {
    width: calc(83.33333% - 30px);
  }

  .about__screen .wrapper {
    height: 3700px;
  }

  .about__screen .wrapper div.about img {
    display: block;
  }

  .about__screen .wrapper div.about img {
    float: none;
  }

  .about__screen .wrapper div.about img {
    margin: 0 auto;
  }
}